#modal__form {
	position: relative;
	width: 420px;
	height: 395px;  //a o  oa
	border-top-left-radius: 25px;
	border-bottom-left-radius: 25px;
	border-bottom-right-radius: 25px;
	background: #fff;
	position: fixed;  //o oo o  o o  o 
	top: 50%;  //oa  45%, oa 5% o 
	left: 50%;  //ooa aa a
	transform: translate(-50%, -50%); //  a o css, oa o    oo   o ooo =)
	display: none;  // oo oo oa  oo 
	opacity: 0;  //oo oao  aoa
	z-index: 8;  //oo oo  ao o o
	padding: 20px 10px;

	.icon-close {
		display: block;
		font-size: 18px;
		text-align: center;
		fill: #fff; } }
//oa a   o  a)

#modal__close {
	position: absolute;
	padding: 16px 0 0 16px;
	top: -20px;
	right: -20px;
	cursor: pointer;
	display: block;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	background-color: $orange;
	transition: background-color 0.3s;
	&:hover {
		background-color: $blue; } }

//ooa


#overlay {
	z-index: 5;  //ooa oa   o o aa, o  o oaoo oa
	position: fixed;  //a a  a
	background-color: rgba(46, 91, 114, 0.5);  //a
	opacity: 0.8;  //o oo oaa
	-moz-opacity: 0.8;  //    
	filter: alpha(opacity=80);
	width: 100%;
	height: 100%;  //ao o  a
	top: 0;  //  a 0, oa oa!
	left: 0;
	cursor: pointer;
	display: none; }  // oo oo  )

.form__box {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-end; }

.modal__title {
	font-size: 20px;
	width: 80%;
	color: $blue;
	margin-top: 20px;
	margin-bottom: 30px; }

.info {
	// margin-top: 30px
	width: 80%;
	input {
		height: 47px;
		width: 100%;
		padding: 0 20px;
		background: $clor_bg;
		border-radius: 25px;
		color: $blue_white; }
	input::-webkit-input-placeholder {
		color: $blue_light; } }

.form__buttons {
	width: 80%;
	margin-top: 30px;
	input {
		width: 40%;
		height: 47px;
		border-radius: 25px;
		color: #fff;
		background-color: $orange;
		transition: background-color 0.3s;
		&:hover {
			background: $blue; } } }

#name_error,
#email_error {
	display: block;
	font-size: 12px;
	color: $orange;
	height: 30px;
	opacity: 0; }
